.infoBox {
  flex: 1;
  background-color: #423d3d !important;
}

.infoBox:not(:last-child) {
  margin-right: 15px;
}
.infoBox__title {
  color: white;
}
.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red.infoBox--selected {
  border-top: 10px solid red;
}

.infoBox_cases--green {
  color: lightgreen !important;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infoBox__total {
  color: white;
  font-weight: 7 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}
